#loginform{
    font-family: Arial;
    display: flex;
    flex-direction: column;
    width: 40%;
    padding: 40px;
    margin:0 auto;
    border: darkcyan;
    box-shadow: 2px 2px 19px #264767;
    transition: 0.6s;
    margin-top: 50px;
    border-radius: 15px;

}


input{
    width: 100%;
    height: 40px;
    margin:0 auto;


}


#imag{
    width: 100px;
    height: 100px;
    margin:0 auto;
    margin-bottom: 20px;
}
#buttons{
    width: 100%;
    height: 40px;
    margin:0 auto;

    align-items: center;
    margin-top: 10px;
    border: darkcyan;
    box-shadow: 2px 2px 6px #264767;
    border-radius: 5px;

}

@media only screen and (max-width: 700px) {
    #loginform{
        width: 320px;
        box-shadow: 2px 2px  10px #04FFD5;
        border-radius: 5px;
        

    }
    input{
        width: 100%;
    }
    #buttons{
        width: 100%;
    }
}